.loader {
  height: 9px;
  width: 30%;
  opacity: 0;
  border: 1px solid #fff;
  border-radius: 10px;
  margin: 10px auto;
  display: block;
  position: relative;
  overflow: hidden;
}

.loader:after {
  content: "";
  height: 100%;
  width: 0;
  background: #ff3d00;
  animation: prog 6s ease-in infinite;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes prog {
  to {
    width: 100%;
  }
}

body {
  background-color: #c9c3c3;
  padding-top: 50px;
}

.visible {
  opacity: 1;
}

.breed-select {
  margin: 0 auto;
  display: none;
}

.cat-info {
  justify-content: center;
  gap: 60px;
  display: flex;
}

.wrap-info {
  width: 40%;
  flex-direction: column;
  display: flex;
}

